import './App.css';
import { Button, Layout, Row, Modal, Col, Empty, Descriptions} from 'antd';
import { QrcodeOutlined } from '@ant-design/icons';
import LectorQR from './components/LectorQR';
import { useState } from 'react';
import AppContext from './AppContext';


const { Header, Content } = Layout;
function App() {

  const [open, setOpen] = useState(false);
  const [qrInfo, setQrInfo] = useState([]);

  console.log(qrInfo)
  const handleClickOpen = () => {
    setOpen(true);
  };

  const sendDataDrive = () => {
    let url = 'https://api.sheety.co/17715b2cd6fcd3bfccb15075ed42228e/bdSat/test';
    let body = {
      test: {
        RFC: "alvv"
      }
    };
    fetch(url, {
      headers: {Authentication: ' Bearer A03E4857C68E8CE5744DAD5D37F03E975FAF0E186E276A81C496755A0D440388'},
      method: 'POST',
      body: JSON.stringify(body)
    })
    .then((response) => response.json())
    .then(json => {
      // Do something with object
      console.log(json.test);
    });
  }


  return (
    <div className="App">
      <AppContext.Provider value={{ qrInfo, setQrInfo}}>
      <Layout>
        <Header style={{background: '#9D2449'}}> <h1 style={{ color: 'white' }}>Lector de codigos QR del SAT</h1></Header>
        <Content>
        <Row type="flex" justify="center" align="middle" className="full-window" style={{marginTop: '2em'}} >
          <Col lg={24} xs={22} type="flex" justify="center" align="middle" >
            <Button size='large' type="primary" icon={<QrcodeOutlined />} onClick={() => handleClickOpen()}>
              Escanear cdigo QR
            </Button>
             <div style={{marginTop: '2em'}}>              
              {
                qrInfo.length>0 ? 
                <div>
                  <Descriptions title="Informacin del SAT" bordered>
                    {qrInfo.map((item) => 
                    
                    <Descriptions.item key={item.key} label={item.key}>{item.value}</Descriptions.item>

                    )}
                  </Descriptions>
                  <Button onClick={() => sendDataDrive()}>Enviar a bd SAT</Button>
                </div>
                
                
                : <Empty/>
              }
            </div> 
              
            </Col>
          </Row>
          <Modal
            title="Escanear cdigo QR "
            centered
            open={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            width={'90vw'} 
          >
            <LectorQR></LectorQR>
          </Modal>

          
        </Content>
      </Layout>
      </AppContext.Provider>
    </div>
  );
}

export default App;
